/*-------------------
 Global Section
 -------------------*/
.App {
  width: 100vw;
  padding: 20px;
}

hr {
  border-top: 1px solid #bbb;
  border-bottom: 1px solid #fff;
}

div {
  display: flexbox;
}

/*-------------------
  Header section
  ------------------- */
section {
  width: 80%;
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

section div.title span.title {
  font-size: 48px;
}
a.github {
  color: black;
}

a.blog {
  color: inherit;
}

a.fb {
  color: #3b5998;
}

a.linkedin {
  color: #0077B5;
}


section div.sns {
  margin-top: 40px;
  font-size: 2vmin;
  width: calc(32vmin);
  display: inline-flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  align-content: center;
}

section div.background {
  margin-top: 30px;
  font-size: 2vmin;
  display: inline-flex;
  align-items: flex-start;
  width: 100%;
}

section div.background ul li{
  flex-direction: column;
}

div.career-details {
  width: 100%;
  margin-top: 20px;
  flex-direction: column;
  align-content: space-between;
}

div.career-details div.item {
  margin-top: 20px;
  width: 100%;
  justify-content: space-between;
  display: inline-flex;
  align-items: flex-end;
  align-content: flex-end;
  flex-direction: row;
}